
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  margin-left: auto;
  margin-right: auto;
}

body {
  font-family: 'Helvetica', sans-serif;
}

.container {
  /* max-width: 1200px; */
  margin: 30px auto;
  overflow: auto;
  min-height: 300px;
  border: 1px solid steelblue;
  padding: 30px;
  border-radius: 5px;
}

.item img {

  width: 100%;
  max-height: 200px;
  height:100%;
  object-fit: cover;

 }

 .transcribingImg img{
  width: 100%;
  max-height: 200px;
  height:100%;
  object-fit: cover;
  filter: brightness(50%);
 }

